

import React, {Component} from "react";

export default class MultiCartMobileCollapsedItemComponent extends Component {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            <div className="l-full-row is-expanded-hide is-expanding-hide">
                <div className="l-page-row u-small-padding-left u-small-padding-right">
                  <div className="u-padding-top u-padding">
                    <div className="g-white1-bg u-box-shadow">

                      <div className="u-padding-left-l u-padding-right-l u-small-padding-left u-small-padding-right">
                        <table className="u-table-fixed">
                          <tbody>
                            <tr>
                              <td className="u-padding-l-xl u-padding-top-l-xl u-padding-right-l u-small-padding-l u-small-padding-top-l">
                                <div className="l-group">
                                    <div className="l-group__element u-vertical-middle u-padding-right-xl u-small-inline-block u-small-padding-right-l">
                                        <span className="u-acc-hide">{this.props.entry.planName}</span>
                                        <span aria-hidden="true" className={"g-icon g-icon--only g-icon--" + this.props.icon}></span>
                                    </div>
                                  {(this.props.entry.terminals && this.props.entry.terminals.length > 0)
                                    && this.props.entry.terminals.map( (terminal, i) => <GroupElement key={"terminal-groupElement_" + i} item={terminal}/> )
                                  }
                                  {(this.props.entry.euroSets && this.props.entry.euroSets.length > 0)
                                    && this.props.entry.euroSets.map( (euroSet, i) => <GroupElement key={"euroSet-groupElement_" + i} item={euroSet}/> )
                                  }
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>

                      <div className="u-border-top u-large-hide u-medium-hide u-padding-all-m u-text-center">
                        <a href="#" className={"opl-checkout__section__title opl-checkout__section__trigger " + this.props.trigger}>Szczegóły pakietu</a>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
        )
    }
}
const GroupElement = ({item}) => {
    return (
        <div className="l-group__element u-vertical-middle u-padding-right-xl u-small-inline-block u-small-padding-right-l">
            <span className="u-acc-hide">{item && item.name}</span>
            <img src={item && item.imageUrl} alt={item.name} className="opl-checkout__image--phone u-block" />
        </div>)
}